} */
.single_ph {
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 400px;
  margin-top: 5px;
  cursor: pointer;
  transition: 0.3s ease;
}
.single_ph:hover {
  -webkit-transform: scale(1.04);
          transform: scale(1.04);
}
