.title {
  font-family: "Marck Script", cursive;
  margin: 10px;
}
.navbar {
  -webkit-align-items: center;
          align-items: center;
  padding: 6px;
  width: 100%;
  z-index: 10;
  display: -webkit-flex;
  display: flex;
  color: rgb(143, 136, 136);
  /* background-color: rgb(34, 34, 34); */
  -webkit-justify-content: space-around;
          justify-content: space-around;
  position: -webkit-sticky;
  position: sticky;
  transition: 0.3s;
  color: rgb(216, 210, 210);
  top: 0;
}
.li {
  transition: 0.3s ease;
  display: inline-block;
  list-style: none;
  transition: 0.3s;
  padding: 0px 15px;
}
.li a {
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  padding: 15px;
  text-decoration: none;
  color: rgb(216, 210, 210);
  transition: 0.3s ease;
}

/* .li:hover {
  background-color: #555;
  color: white;
} */

/* .li:hover a {
  color: white;
} */
.li a::after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: #fff;
  transition: width 0.3s;
}
.li a:hover::after {
  width: 100%;
  transition: width 0.3s;
}
.tog {
  position: absolute;
  width: 30px;
  height: 20px;
  display: none;
  /* border: 1px solid white; */
  -webkit-flex-direction: column;
          flex-direction: column;
  right: 1rem;
  cursor: pointer;
  /* background-color: rgb(34, 34, 34); */
  top: 1.3rem;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  /* align-items: center; */
}
.cls {
  position: absolute;
  top: 1rem;
  display: none;
  right: 1rem;
}
.hamb {
  border-radius: 20px;
  width: 100%;
  height: 3px;
  background-color: rgb(216, 210, 210);
}
@media (max-width: 700px) {
  .navbar {
    /* display: block; */
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
  }
  /* .title {
    text-align: left;
    margin: 10px; 
  } */
  .li {
    display: block;
  }
  /* .li a {
    display: block;
  } */
  .tog {
    display: -webkit-flex;
    display: flex;
    top: 1rem;
  }
  .menu {
    width: 100%;
    height: 100vh;
    /* display: none; */
    position: absolute;
    left: -100%;
    top: 0;
    /* z-index: 111; */
    text-align: left;
    padding-top: 5rem;
    transition: 0.3s;
    -webkit-align-items: center;
            align-items: center;
  }
  .menu.active {
    z-index: 111;
    left: 0;
    background-color: rgb(34, 34, 34);
  }
  /* .cls {
    right: 1rem;
  } */
  .title {
    font-size: 26px;
  }
  .li {
    padding: 0 25px;
  }
  .li a {
    font-size: 18px;
  }
  .li:hover {
    background-color: #555;
  }
  .cls {
    display: block;
  }
}

